
:root {
  --color: rgb(48, 194, 238);
}
.App {
  font-family: sans-serif;
  background: red;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  /*color: rgba(24, 126, 243, 0.849);*/
  color: rgb(48, 194, 238);
  color: var(--color);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 100%;
  height: 100%
}

.Buttons {
  width: 40px;
}

.ClockController {
  position: fixed;
  
  margin: auto;
  top: 0px;
  width: 20px;
  height: 20px;
  z-index: 2003;
  font-size: 20px; 
  -webkit-font-feature-settings: "kern" off; 
          font-feature-settings: "kern" off; 
  -webkit-font-kerning: none; 
          font-kerning: none;
  right: 15px;
  
}
.DateTimeLabel {
  position: fixed;
  display: block;
  margin: auto;
  background:rgba(77, 91, 129, 0);
  top: 5px;
  width: 400px;
  height: 80px;
  z-index: 2000;
  font-family: arial, sans-serif;
  font-weight: light;
  text-shadow: 4px 4px 4px  rgba(43, 48, 5, 0.826);
  -webkit-font-feature-settings: "kern" off;
          font-feature-settings: "kern" off;
  -webkit-font-kerning: none;
          font-kerning: none;
  left: 5px;
  margin-left: -200px;
}
.Fullscreen {
  position: fixed;
  z-index: 2000;
}
.TimeSwipe {
  position: fixed;
  bottom: 1px;
  background: rgb(77, 91, 129);
  width: 100%;
  z-index: 2001;
}
.TimeSelectorh {
  position: fixed;
  bottom: 0px;
  left: 0px;
  margin-left: 0px;
  width: 100%;
  height: 100px;
  z-index: 2001;
}
.TimeSelectorv {
  position: fixed;
  top: 0px;
  right: 0px;
  margin-top: 0px;
  width: 100px;
  height: 100%;
  z-index: 2001;
}

.Globe {
  position: fixed;
  background: black;
  top: 0;
  left:0px;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.Miniglobe {
  position: fixed;
  box-shadow: 2px 2px 5px rgba(37, 38, 39, 0.397);
  background:  linear-gradient(rgb(67, 124, 199), #111) repeat scroll 0 0 #222;
  height: 100px;
  width: 200px;
  top: 20px;
  left: 20px;
  z-index: 2; 
}

.TimeContainer {
    position: relative;
    background: rgba(26, 60, 99, 0.0);
    overflow: hidden;
    background: rgba(26, 60, 99, 0);
    font-family: verdana, sans-serif;
    text-shadow: 1px 1px 3px  rgba(43, 48, 5, 0.826);
}

.Mask {
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(47, 99, 26, 0);
    -webkit-mask-image: linear-gradient(to right, rgba(42, 177, 42, 0), rgba(0,0,0,1), rgba(48, 139, 151, 0));
    -mask-image: linear-gradient(to bottom, rgba(42, 177, 42, 0.514), rgba(0,0,0,1), rgba(0, 0, 0, 0.425));
}
.Mask-v {
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(47, 99, 26, 0);
    -webkit-mask-image: linear-gradient(to bottom, rgba(42, 177, 42, 0), rgba(0,0,0,1), rgba(48, 139, 151, 0));
    -mask-image: linear-gradient(to bottom, rgba(42, 177, 42, 0.514), rgba(0,0,0,1), rgba(0, 0, 0, 0.425));
}



.TimeScale {
    position: relative;
    white-space: nowrap;

    -webkit-font-feature-settings: "kern" off;

            font-feature-settings: "kern" off;

    -webkit-font-kerning: none;

            font-kerning: none;
}

.DayTic {
    position: absolute;
    pointer-events: none;
    font-size: 12px; 
    bottom: 10px
}
