.FixedLabel {
    position: absolute;
    left: 50%;
    bottom: 30px;
    margin-left: -1px;
    width: 80px;
    height: 60px;
    background: rgba(181, 82, 219, 0);
   
}
.FixedLabel-v {
    position: absolute;
    top: 50%;
    right:0px;
    margin-top: -30px;
    width: 180px;
    height: 100px;
    background: rgba(82, 219, 144, 0)
}

.DayLabel {
    position: absolute;
    pointer-events: none;
    font-size: 58px; 
    top: 0px;
    left: 0px
}
.TimeLabel {
    position: absolute;
    pointer-events: none;
    font-size: 25px;
    top: 58px;
    left: 12px;
}
.MonthLabel {
    position: absolute;
    pointer-events: none;
    top: 6px;
    font-size: 28px; 
    left: 70px
}
.YearLabel {
    position: absolute;
    pointer-events: none;
    font-size: 22px; 
    top: 35px;
    left: 70px
}
